
import { createStore } from 'vuex'
import auth from './auth' 
import info from './info'
import exchange from './exchange';
import wallet from './wallet';
import { loader } from './loader';
import localizeFilter from '@/filters/localize.filter'

// Create a new store instance.
export default createStore({
    state: () => ({
      isLoggined: (localStorage.exchangeClientSession !== null) && (localStorage.exchangeClientSession !== undefined),
      error: null,
      locale: localStorage.exchangeClientLocale ?? "ru-RU",
      currency: "USD",
      // set isDemo = false by default when add release mode
      isDemo: true
    }),
    getters: {
        error: s => s.error,
        isLoggined: state => {
            return state.isLoggined
        },
        locales() {
            return ['en-US', 'ru-RU']
        },
        coins() {
            return [
                {name: 'DOM', localizedName: 'DOM', ticker: 'DOM', canBuy: false },
                {name: 'GadgetCoin', localizedName: 'Gadget Coin', ticker: 'GDC', canBuy: true},
                {name: 'CapitalWhalePresaleToken', localizedName: 'CWPT', ticker: 'CWPT', canBuy: true},
                {name: 'CuprumToken', localizedName: 'Cuprum Token', ticker: 'CUT', canBuy: true},
                {name: 'InternetCoin', localizedName: 'Internet Coin', ticker: 'ITC', canBuy: true},
                {name: 'OilGasCoalToken', localizedName: 'Oil Gas Coal Token', ticker: 'OGCT', canBuy: true},
                {name: 'DreamLifeCoin', localizedName: 'Dream Life Coin', ticker: 'DRMC', canBuy: true, canBuyToBonusBalance: true},
                // {name: 'TRCoin', localizedName: 'TRCoin', ticker: 'TRC', canBuy: true},
            ]
        },
        coinByTicker: (state, getters) => (ticker) => {
            return getters.coins.filter((el)=>{
                return el.ticker === ticker
            })[0]
        },
        coinByName: (state, getters) => (name) => {
            return getters.coins.filter((el)=>{
                return el.name === name
            })[0]
        },
        // TODO: remove this flag and its occurrences
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        isDemo: (state, getters) => {
            return state.isDemo
        },
    },
    modules: {
        auth, info, loader, exchange, wallet
    },
    mutations: {
        SET_LOGGINED(state, data) {
            state.isLoggined = data
        },
        SET_ERROR(state, error) {
            state.error = error
        },
        SET_LOCALE(state, locale) {
            state.locale = locale
            localStorage.exchangeClientLocale = locale
        },
        CLEAR_ERROR(state) {
            state.error = null
        },
        SET_DEMO(state, data) {
            // uncomment this when add release mode
            // state.isDemo = data.isDemo
        }
    },
    actions: {
        // fetch locale/currency/etc
    }
  })
