const router = createRouter({
  mode: 'history',
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const session = localStorage.exchangeClientSession
  to.hash = ''
  document.title = to.meta.title ? to.meta.title : "Capital Whale"
  if (to.meta.requiresAuth && (session == null ||  session == undefined)) {
    next({ name: 'login' })
  }
  else
   next()
})

export default router
