    runDomBurnTimer({dispatch, commit}, {timestamp}) {
      let timeLeft = timestamp - (+new Date())
      if (timeLeft > 1000) {
        commit('SET_DOM_BURN_TIME_LEFT', timeLeft)
        let interval = setInterval(() => {
          if (timeLeft <= 0) {
            clearInterval(interval)                
          } else {
            // console.log(timeLeft)
            timeLeft-=1000
            commit('SET_DOM_BURN_TIME_LEFT', timeLeft)
          }             
        }, 1000)
        commit('SET_DOM_BURN_TIMER', interval)
        }
      },

