function displayBigIntAsDecimal(value, decimalsNumber, leaveDigitsAfterDot) {
  const decimals = decimalsNumber.toString().length - 1;
  if(!leaveDigitsAfterDot) leaveDigitsAfterDot = decimals;
  if(decimals >= value.toString().length) {
    return '0.' + ('0'.repeat(decimals - value.toString().length) + value.toString()).slice(0, leaveDigitsAfterDot);
  }
  return value.toString().slice(0, -decimals) + '.' + value.toString().slice(-decimals).slice(0, leaveDigitsAfterDot);
}

